.column {
    @extend %lost-clearfix;
}

.column--2 {
    .column__item {
        @include media('above-tablet') {
            lost-column: 1/2;
        }

        @include media('below-tablet') {
            lost-column: 1/1;
        }
    }

    &.column--no-gap {
        .column__item {
            @include media('above-tablet') {
                lost-column: 1/2 0 0;
            }

            @include media('below-tablet') {
                lost-column: 1/1;
            }
        }
    }
}

.column--3 {
    .column__item {
        @include media('above-tablet') {
            lost-column: 1/3;
        }

        @include media('below-tablet') {
            lost-column: 1/1;
        }
    }
}

.column--12 {
    .column__item--5 {
        lost-column: 5/12 12 50px;
    }

    .column__item--7 {
        lost-column: 7/12 12 50px;
    }
}

